<style scoped lang="less">
@import "../../css/common.less";
.label {
  width: 100%;
  padding: unit(30/@rem, rem 0);
  input {
    display: block;
    line-height: unit(60/@rem, rem);
    width: 100%;
  }
}
</style>
<template>
  <div class="label">
    <input :type="type" :placeholder="placeholder" @input="anyiInput">
  </div>
</template>
<script>
export default {
  data() {
    return {};
  },
  props: {
    value: "",
    type: {
      type: String,
      default: "text"
    },
    placeholder: "",
    maxLength: {
      type: Number,
      default: 4
    }
  },
  model: {
    event: "input"
  },
  methods: {
    anyiInput(e) {
      var value = e.target.value;
      if (this.type === "number") {
        if (value.indexOf(".") > -1) {
          value.replace(/./g, "");
        }
      }
      if (value.length > this.maxLength) {
        value = value.slice(0, this.maxLength);
      }
      e.target.value = value;
      this.$emit("input", value);
    }
  }
};
</script>



