<style scoped lang="less">
@import "../css/common.less";
.login {
  .content {
    overflow: hidden;
    // 顶部logo
    .top-logo {
      padding-top: unit(40/@rem, rem);
      padding-bottom: unit(100/@rem, rem);
      .img {
        height: unit(50/@rem, rem);
        margin-right: unit(20/@rem, rem);
      }
      .tit {
        color: @txt-lv6;
        line-height: unit(50/@rem, rem);
      }
    }

    //验证码登录
    .login-code,
    .login-pwd {
      padding: 0 unit(58/@rem, rem);
      position: relative;
      .form-item {
        border-bottom: 1px solid @borderColor;
        .phone-code {
          flex: 0 0 auto;
        }
        .change-show {
          flex: 0 0 auto;
          padding: 0 unit(10/@rem, rem);
          height: unit(40/@rem, rem);
          width: unit(40/@rem, rem);
        }
        .img {
          height: unit(40/@rem, rem);
          width: unit(40/@rem, rem);
        }
        .forget-pwd {
          color: @txt-lv3;
          position: absolute;
          right: unit(58/@rem, rem);
          bottom: unit(-70/@rem, rem);
        }
      }
    }
    //登录按钮
    .submit {
      margin: 0 unit(58/@rem, rem);
      margin-top: unit(105/@rem, rem);
      margin-bottom: unit(30/@rem, rem);
      text-align: center;
      height: unit(98/@rem, rem);
      line-height: unit(98/@rem, rem);
    }

    //切换登录类型
    .change-login-type {
      height: unit(60/@rem, rem);
      padding: unit(16/@rem, rem) 0;
      text-align: center;
      span {
        color: @txt-lv4;
      }
    }

    //协议
    .tip {
      color: @txt-lv3;
      margin-top: unit(200/@rem, rem);
      margin-bottom: unit(100/@rem, rem);
    }
  }
}
</style>
<template>
  <div class="login flex">
    <div class="content f-1">
      <!-- 顶部logo -->
      <div class="top-logo flex row center">
        <img class="img" src="../assets/logo@3x.png">
        <h2 class="tit fz-48 ">风险管家</h2>
      </div>
      <!-- 验证码登录 -->
      <div class="login-code" v-show="loginType === 'phone'">
        <div class="form-item">
          <anyiInput :maxLength="11" type="number" placeholder="请输入手机号" v-model="formData.phone" />
        </div>
        <div class="form-item-verifyCode form-item flex row space item-center">
          <anyiInput :maxLength="4" type="text" placeholder="请输验证码" v-model="formData.verifyCode" />
          <!-- 图形验证码 -->
          <div @click="getCaptcha(true)" class="svg-img flex center" v-html="captchaImg">
          </div>
        </div>
        <div class="form-item flex row space item-center">
          <anyiInput :maxLength="4" type="number" placeholder="请输入手机验证码" v-model="formData.code" />
          <!-- 获取手机验证码 -->
          <div @click="getCode" class="phone-code anyi-btn-txt fz-32" :class="{'anyi-btn-txt-disabled':codeDisabled || txtDisaled}">
            {{txtConfig.getCodeTxt}}
          </div>
        </div>
      </div>

      <!-- 账号密码登录 -->
      <div class="login-pwd" v-show="loginType=== 'pwd'">
        <div class="form-item">
          <anyiInput :maxLength="16" type="text" placeholder="请输入手机号码/用户名" v-model="formData.account" />
        </div>
        <div class="form-item flex row space item-center">
          <anyiInput :maxLength="16" :type="passwordType" placeholder="请输入密码" v-model="formData.password" />
          <div class="change-show flex center">
            <img @click="changeShowPwd" class="img" src="../assets/icon-不可见@3x.png" v-if="passwordType === 'password'">
            <img @click="changeShowPwd" class="img" src="../assets/icon-可见@3x.png" v-if="passwordType === 'text'">
          </div>
          <!-- 忘记密码 -->
          <span class="forget-pwd" @click="$link('/forget')">
            忘记密码
          </span>
        </div>
      </div>

      <!-- 登录按钮 -->
      <div @click="submit" class="anyi-btn submit fz-36" :class="{'anyi-btn-disabled': btnDisabled}">
        {{txtConfig.formType}}
      </div>

      <!-- 切换登录类型 -->
      <div class="change-login-type">
        <span class="fz-28" v-if="loginType === 'phone'" @click="loginType = 'pwd'">密码登录</span>
        <span class="fz-28" v-if="loginType === 'pwd'" @click="loginType = 'phone'">快速登录</span>
      </div>

      <!-- 协议 -->
      <div class="tip flex row center" v-if="loginType === 'phone'">
        <span class="fz-28">进入风险管家代表你已同意</span>
        <span class="fz-28" style="color:#2A2A2A">《风险管家用户协议》</span>
      </div>
    </div>
  </div>
</template>
<script>
import { Toast } from "mint-ui";
import anyiInput from "../components/common/anyiInput";
export default {
  components: {
    anyiInput
  },
  data() {
    return {
      //表单参数对象
      formData: {
        phone: "", //手机号
        verifyCode: "", //图形验证码
        code: "", //手机验证码
        account: "", //用户名
        password: "" //密码
      },
      //文案对象
      txtConfig: {
        getCodeTxt: "获取验证码",
        formType: "进入风险管家"
      },
      codeDisabled: false,
      captchaImg: "",
      timer: "",
      loginType: "phone", //登录方式
      passwordType: "password"
    };
  },
  computed: {
    //获取手机验证码是否禁用
    txtDisaled() {
      if (
        this.formData.verifyCode.length < 4 ||
        this.formData.phone.length < 11
      ) {
        return true;
      }
      return false;
    },
    //登录按钮是否禁用
    btnDisabled() {
      switch (this.loginType) {
        case "phone":
          if (
            this.formData.verifyCode.length < 4 ||
            this.formData.phone.length < 11 ||
            this.formData.code.length < 4
          ) {
            return true;
          }
          return false;
          break;
        case "pwd":
          if (
            this.formData.verifyCode.account < 5 ||
            this.formData.password.length < 5
          ) {
            return true;
          }
          return false;
          break;
      }
    }
  },
  created() {
    //有token 直接跳转到首页
    // if (this.$http.getCookie("sso-token")) this.$link("/home");
    //获取登录类型
    if (this.$route.query.type && this.$route.query.type === "pwd") {
      this.loginType = "pwd";
    }
  },
  mounted() {
    document.querySelector(".login").addEventListener(
      "touchmove",
      e => {
        e.preventDefault();
        e.stopPropagation();
      },
      true
    );
    //获取图形验证码
    this.getCaptcha(true);
  },
  methods: {
    //登录按钮 成功跳转
    submit() {
      if (this.btnDisabled) return;
      //校验 按类型校验
      switch (this.loginType) {
        case "phone": //手机登录
          //校验手机和短信验证码
          var ruls = [
            this.$valid.ruls.phone(this.formData.phone),
            this.$valid.ruls.code(this.formData.code)
          ];
          if (!this.$valid.validate(ruls)) return;
          //成功执行登录逻辑
          this.$http
            .loginByPhone({
              data: { account: this.formData.phone, code: this.formData.code }
            })
            .then(res => {
              this.$link("/home");
            });
          break;
        case "pwd": //手机登录
          var ruls = [
            this.$valid.ruls.account(this.formData.account),
            this.$valid.ruls.password(this.formData.password)
          ];
          if (!this.$valid.validate(ruls)) return;
          //成功执行登录逻辑
          this.$http
            .login({
              data: {
                account: this.formData.account,
                password: this.formData.password
              }
            })
            .then(res => {
              this.$link("/home");
            });
          break;
      }
    },
    //修改密码显示
    changeShowPwd() {
      if (this.passwordType === "password") {
        this.passwordType = "text";
      } else {
        this.passwordType = "password";
      }
    },
    //获取验证码
    getCode() {
      //校验手机号码 获取验证码后重新刷新一下图形验证码 但是不显示
      if (this.txtDisaled) return;
      var ruls = [
        this.$valid.ruls.phone(this.formData.phone),
        this.$valid.ruls.verifyCode(this.formData.verifyCode)
      ];
      if (!this.$valid.validate(ruls)) return;
      if (!this.codeDisabled) {
        this.$http
          .getCode({
            data: {
              phone: this.formData.phone,
              verifyCode: this.formData.verifyCode
            }
          })
          .then(
            res => {
              //成功后重置验证码
              this.getCaptcha();
              this.setBtnTxt();
              //点击成功后 获取ajax 获取code
              Toast({
                message: "验证码已发送至您手机",
                position: "center",
                duration: 2000
              });
            },
            err => {}
          );
      }
    },
    //获取图形验证码
    getCaptcha(params) {
      //传入true未显示 不传或false 不显示
      this.$http.getCaptcha().then(res => {
        if (params) {
          this.captchaImg = res.result.svg;
        }
      });
    },
    //动态设置按钮文案
    setBtnTxt() {
      //清除时钟
      clearInterval(timer);
      this.codeDisabled = true;
      var index = 60;
      this.txtConfig.getCodeTxt = index + "秒后重新获取";
      var timer = setInterval(() => {
        index--;
        //动态设置文案
        this.txtConfig.getCodeTxt = index + "秒后重新获取";
        if (index <= 0) {
          //还原文案
          this.txtConfig.getCodeTxt = "获取验证码";
          this.codeDisabled = false;
          clearInterval(timer);
        }
      }, 1000);
      //获取验证码
    },
    //忘记密码跳转
    forget() {
      //登录跳转 现在暂时内部跳转
      window.location.href = "/#/forget";
    }
  }
};
</script>



